	module.exports = {"data":{"wordpressPost":{"title":"How to Get This Sold Out Benefit Blush Kit","content":"<p>Every now and then a product pops up and you quite simply <strong>HAVE</strong> to have it.</p>\n<p>Unfortunately, you&#8217;re generally not the only one who feels that way which means the product you simply <strong>CANNOT LIVE WITHOUT</strong> sells out the moment it&#8217;s released leaving you to, well, live without it.</p>\n<p>Take <a href=\"https://www.sephora.com.au/brands/benefit-cosmetics?gclid=CKLPpfCUsNMCFRV_vQodAZIOuQ\">Benefit&#8217;s Cheek Parade &#8211; Beauty &amp; the Beat Blush Kit</a> for example. This sweet as palette (which contains five full-size box o&#8217; powder blushes) sold out within 24 hours when it was released in the US. This meant thousands were left disappointed.</p>\n<p>But, and this a <strong>BIG BUT</strong>, we have excellent news. The good folk at Benefit are doing a re-release and the palette will be back on sale in Australia on May 5 for $99. If you don&#8217;t want to chance missing out again you can ore-order it <strong>NOW</strong> <a href=\"https://www.sephora.com.au\">here</a>.</p>\n<p>Yip!</p>\n<img class=\"alignnone size-medium wp-image-1735\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/19190043/Benefit-Cosmetics-Cheek-Parade-99-300x258.jpg\" alt=\"\" width=\"300\" height=\"258\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/19190043/Benefit-Cosmetics-Cheek-Parade-99-300x258.jpg 300w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/19190043/Benefit-Cosmetics-Cheek-Parade-99-768x659.jpg 768w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/19190043/Benefit-Cosmetics-Cheek-Parade-99-1024x879.jpg 1024w\" sizes=\"(max-width: 300px) 100vw, 300px\" />\n<p><strong>Cheek Parade contains full-sized versions of California Girl Golden Pink Blush, Hoola Lite, Hoola Matte Bronzing Powder, Dandelion and Rockateur plus comes with its own brush making touchups easier than ever. </strong></p>\n<p>&nbsp;</p>\n","better_featured_image":{"source_url":"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/19185225/Benefit-Cosmetics-Cheek-Parade-99_open.jpg"},"categories":[{"slug":"makeup","name":"Makeup"}]},"site":{"siteMetadata":{"title":"The Beauty Insider"}}},"pathContext":{"id":"c048d7ac-5769-540a-abf0-b996478483ac"}}

